.main {
  display: flex;
  flex-flow: column;
  padding-top: 65px;
  padding-bottom: 3vw;
  align-items: center;

  @media (min-width: $viewport-large) {
    padding-top: 85px;
  }
}

.bg__img {
  position: relative;
  width: 100vw;
  height: 475px;
  overflow: hidden;

  @media (min-width: $viewport-large) {
    height: 800px;
  }

  &-main { 
    position: absolute; 
    top: 50%;
    left: 50%;
    transform: translate(-32%, -50%);

    @media (min-width: $viewport-mid) {
      transform: translate(-50%, -60%);
    } 
    
    @media (min-width: $viewport-large) {
      bottom: 0;
      width: 100vw;
      height: 800px;
      object-fit: cover;
    }  
  }
}

.bg {
  position: relative;
}

.bg__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  width: auto;
  height: auto;

  @media (min-width: $viewport-mid) {
    transform: translate(-50%, -135%);

    &-img {
      width: 35vw;
      height: auto;
    }
  }

  &-img {
    position: relative;
    width: 75vw;
    height: auto;
  }

  @media (min-width: $viewport-large) {
    position: absolute;
    top: 145px;
    left: 50%;
    transform: translate(-50%, -3vh);
    width: auto;
    height: auto;

    &-img {
      width: 517px;
      height: 200px;
      color: $white;
    }
  }
}